<template>
    <div class="container">
        <router-link to="/" class="btn grey right">رجوع</router-link>
        <h3>اضافة متابعة مشروعات</h3>
        <form @submit="saveField" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="field_id" placeholder="الرقم التعريفي لمتابعة المشروعات" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="name" placeholder="اسم متابعة المشروعات" required>
                </div>
            </div>
            <button type="submit" class="btn btn-large lite-blue right">تسجيل</button>
        </form>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-field',
    data() {
        return {
            field_id: null,
            name: null,
            projects: [],      
        }
    },
    methods: {
        saveField() {
            db.collection('fields').add({
                field_id: this.field_id,
                name: this.name,
                projects: [],
            })
            .then(docRef => {
                this.$router.push('/')
            })
        },
    }
}
</script>


