<template>
    <div class="container">
        <h3>New Section</h3>
        <form @submit="saveSection" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="section_id" placeholder="Section ID" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_name" placeholder="Section description" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_info" placeholder="Section info" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="contract_value" placeholder="Contract value" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="approved_30_6_20" placeholder="Approved until 30/6/2020" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="approved_20_21" placeholder="Approved 2020/2021" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="total_approved_2021" placeholder="Total approval until 30/6/2021" required>
                </div>
            </div>

            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="company_id" placeholder="Company ID">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="company_name" placeholder="Company name">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="outgoing" placeholder="The outgoing">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="execution_rate" placeholder="The real execution rate">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="company_contract_value" placeholder="Contract value for this company">
                </div>
            </div>

            <button type="submit" class="btn btn-large lite-blue right">Submit</button>
        </form>
    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
    name: 'new-section',
    data() {
        return {
            section_id: null,
            section_name: null,
            section_info: null,
            contract_value: null,
            approved_30_6_20: null,
            approved_20_21: null,
            total_approved_2021: null,
            company_id: null,
            company_name: null,
            outgoing: null,
            execution_rate: null,
            company_contract_value: null,
            sections: [],
        }
    },
    mounted() {
        var database = db.collection('fields')
        database.where('project_id', '==', this.$route.params.project_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc.data().sections)
            })
        })
    },
    methods: {
        saveSection() {
            this.sections.push({
                section_id: this.section_id,
                section_name: this.section_name,
                section_info: this.section_info,
                contract_value: this.contract_value,
                approved_30_6_20: this.approved_30_6_20,
                approved_20_21: this.approved_20_21,
                total_approved_2021: this.total_approved_2021,
                companies: [
                    {
                        company_id: this.company_id,
                        company_name: this.company_name,
                        outgoing: this.outgoing,
                        execution_rate: this.execution_rate,
                        contract_value: this.company_contract_value,
                    }
                ]
            })
            firebase.firestore.FieldValue.arrayUnion(...['javascript', 'typescript'])
            var database = db.collection('fields/projects/sections')
            database.where('field_id', '==', this.$route.params.field_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.data().projects.forEach(project => {
                        if (project.project_id == this.$route.params.project_id) {
                            // database.doc(doc.id).collection('projects/sections').get().then(snapshot => {
                            //     console.log(snapshot.docs)
                            // })
                            
                        }
                    })

                })
            })
            // database.where('field_id', '==', this.$route.params.field_id).get().then(querySnapshot => {
            //     querySnapshot.forEach(doc => {
            //         doc.data().projects.forEach(project => {
            //             if (project.project_id == this.$route.params.project_id) {
            //                 project.set({
            //                     sections: [
            //                     {
            //                         section_id: this.section_id,
            //                         section_name: this.section_name,
            //                         section_info: this.section_info,
            //                         contract_value: this.contract_value,
            //                         approved_30_6_20: this.approved_30_6_20,
            //                         approved_20_21: this.approved_20_21,
            //                         total_approved_2021: this.total_approved_2021,
            //                         companies: [],
            //                     },
            //                     ],
                            
            //                 }, { merge: true })
            //                 .then(docRef => {
            //                     this.$router.push('/')
            //                 })
            //             }
            //         })
            //     })
            // }) 
        }
    }
}
</script>


