<template>
    <div id="new-project"> 
        <h3>مشروع جديد</h3>
        <form @submit="saveProject">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="project_id" placeholder="الرقم التعريفي للمشروع" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="project_desc" placeholder="وصف المشروع" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="project_info" placeholder="بيانات عن المشروع" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="residual" placeholder="المتبقي" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="date" v-model="to_start" placeholder="موعد البدء" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="date" v-model="to_end" placeholder="موعد النهو" >
                </div>
            </div>

            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="section_id" placeholder="الرقم التعريفي للقطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_name" placeholder="وصف القطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_info" placeholder="بيانات عن القطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="contract_value" placeholder="قيمة العقد" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="approved_30_6_20" placeholder="المعتمد حتى 30/6/2020" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="approved_20_21" placeholder="المعتمد 2020/2021" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="total_approved_2021" placeholder="اجمالي المعتمد حتى 30/6/2021" required>
                </div>
            </div>

            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="company_id" placeholder="الرقم التعريفي للشركة">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="company_name" placeholder="اسم الشركة">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="outgoing" placeholder="المنصرف">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="execution_rate" placeholder="نسبة التنفيذ الفعلية">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="company_contract_value" placeholder="قيمة التعاقد للشركة">
                </div>
            </div>

            <button type="submit" class="btn right">تسجيل</button>
        </form>
    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
    name: 'new-project',
    data() {
        return {
            project_id: null,
            project_desc: null,
            project_info: null,
            residual: null,
            to_start: null,
            to_end: null,
            section_id: null,
            section_name: null,
            section_info: null,
            contract_value: null,
            approved_30_6_20: null,
            approved_20_21: null,
            total_approved_2021: null,
            company_id: null,
            company_name: null,
            outgoing: null,
            execution_rate: null,
            company_contract_value: null,
            projects: [],
        }
    },
    methods: {
        saveProject() {
            this.projects.push({
                project_id: this.project_id,
                project_desc: this.project_desc,
                project_info: this.project_info,
                residual: this.residual,
                to_start: this.to_start,
                to_end: this.to_end,
                sections: [
                    {
                        section_id: this.section_id,
                        section_name: this.section_name,
                        section_info: this.section_info,
                        contract_value: this.contract_value,
                        approved_30_6_20: this.approved_30_6_20,
                        approved_20_21: this.approved_20_21,
                        total_approved_2021: this.total_approved_2021,
                        companies: [
                            {
                                company_id: this.company_id,
                                company_name: this.company_name,
                                outgoing: this.outgoing,
                                execution_rate: this.execution_rate,
                                contract_value: this.company_contract_value,
                            }
                        ]
                    }
                ],
            })
            firebase.firestore.FieldValue.arrayUnion(...['javascript', 'typescript'])
            var database = db.collection('fields')
            database.where('field_id', '==', this.$route.params.field_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    database.doc(doc.id)
                    .update('projects', firebase.firestore.FieldValue.arrayUnion.apply(null, this.projects))
                    .then(docRef => {
                        this.$router.push('/')
                    })
                })
            })
        }
    }
}
</script>


